.menu {
  box-sizing: border-box;
  height: 100vh;
  background: rgba(194, 194, 194, 1);
  font-weight: 600;
  text-transform: uppercase;

  &__top {
    margin-bottom: 24px;

    @include tablet {
      margin-bottom: 8px;
    }
  }

  &__nav {
    margin-top: 32px;
    margin-bottom: 40px;
  }

  &__phone-number {
    display: block;
    color: #1d1d1d;
    text-decoration: none;
    font-size: 16px;
    line-height: 27px;
    margin-bottom: 16px;
  }

  &__book-test {
    display: block;
    width: fit-content;
    color: #1d1d1d;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
    text-decoration: none;
    padding-bottom: 7px;
    border-bottom: 1px solid #1d1d1d;
  }
}
